'use client';

import { useState, useEffect } from 'react';
import { Key, Link as LinkIcon, Check, X, Save, Eye, EyeOff, RefreshCw, AlertCircle } from 'lucide-react';
import { createClient } from '@/lib/supabase/client';

interface AdAccount {
  id: string;
  platform: string;
  account_name: string;
  status: string;
  last_synced_at: string | null;
}

export default function SettingsPage() {
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState('ad-platforms');
  const [adAccounts, setAdAccounts] = useState<AdAccount[]>([]);
  const [syncing, setSyncing] = useState(false);
  
  // API Keys state
  const [showKeys, setShowKeys] = useState<Record<string, boolean>>({});
  const [openaiKey, setOpenaiKey] = useState('');
  const [anthropicKey, setAnthropicKey] = useState('');
  const [googleKey, setGoogleKey] = useState('');
  
  // Default model
  const [defaultModel, setDefaultModel] = useState('anthropic/claude-sonnet-4-5');
  
  // Demo mode
  const [demoMode, setDemoMode] = useState(false);

  const supabase = createClient();

  // Load connected ad accounts from database
  useEffect(() => {
    loadAdAccounts();
  }, []);

  const loadAdAccounts = async () => {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) return;

      const { data: profile } = await supabase
        .from('profiles')
        .select('tenant_id')
        .eq('id', user.id)
        .single();

      if (!profile?.tenant_id) return;

      const { data, error: loadError } = await supabase
        .from('ad_accounts')
        .select('id, platform, account_name, status, last_synced_at')
        .eq('tenant_id', profile.tenant_id)
        .order('platform');

      if (loadError) {
        console.error('Error loading ad accounts:', loadError);
        return;
      }

      if (data) {
        setAdAccounts(data);
      }
    } catch (err) {
      console.error('Error loading ad accounts:', err);
    }
  };

  const handleSyncData = async () => {
    setSyncing(true);
    setError(null);
    setSuccess(null);

    try {
      // Call the sync API (uses same backend as main branch)
      const response = await fetch('/api/sync-data', {
        method: 'POST',
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || 'Failed to sync data');
      }

      const results = data.results || [];
      const totalCampaigns = results.reduce((sum: number, r: any) => sum + (r.campaigns || 0), 0);
      const totalMetrics = results.reduce((sum: number, r: any) => sum + (r.metrics || 0), 0);

      setSuccess(`Synced ${totalCampaigns} campaigns and ${totalMetrics} metric records!`);
      await loadAdAccounts();
    } catch (err: any) {
      setError(err.message || 'Failed to sync data');
    } finally {
      setSyncing(false);
    }
  };

  const toggleKeyVisibility = (key: string) => {
    setShowKeys(prev => ({ ...prev, [key]: !prev[key] }));
  };

  const handleSaveAPIKeys = async () => {
    setLoading(true);
    setError(null);
    setSuccess(null);

    try {
      localStorage.setItem('openai_key', openaiKey);
      localStorage.setItem('anthropic_key', anthropicKey);
      localStorage.setItem('google_key', googleKey);
      localStorage.setItem('default_model', defaultModel);
      localStorage.setItem('demo_mode', String(demoMode));
      
      setSuccess('Settings saved successfully!');
    } catch (err: any) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  const getPlatformDisplayName = (platform: string) => {
    return platform.replace('_ads', ' Ads').replace(/^\w/, c => c.toUpperCase());
  };

  const tabs = [
    { id: 'ad-platforms', label: 'Ad Platforms' },
    { id: 'ai-models', label: 'AI Models' },
    { id: 'preferences', label: 'Preferences' },
  ];

  return (
    <div className="p-6 space-y-6">
      <div>
        <h1 className="text-3xl font-bold">Settings</h1>
        <p className="text-gray-400 mt-1">Manage your ad platform connections and AI models</p>
      </div>

      {success && (
        <div className="bg-green-500/20 border border-green-500/30 text-green-400 px-4 py-3 rounded-lg flex items-center gap-2">
          <Check className="w-5 h-5" />
          {success}
        </div>
      )}

      {error && (
        <div className="bg-red-500/20 border border-red-500/30 text-red-400 px-4 py-3 rounded-lg flex items-center gap-2">
          <X className="w-5 h-5" />
          {error}
        </div>
      )}

      {/* Tabs */}
      <div className="flex gap-2 border-b border-white/10 pb-2">
        {tabs.map(tab => (
          <button
            key={tab.id}
            onClick={() => setActiveTab(tab.id)}
            className={`px-4 py-2 rounded-lg transition-colors ${
              activeTab === tab.id
                ? 'bg-purple-500/20 text-purple-300 border border-purple-500/30'
                : 'text-gray-400 hover:bg-white/5'
            }`}
          >
            {tab.label}
          </button>
        ))}
      </div>

      {/* Ad Platforms Tab */}
      {activeTab === 'ad-platforms' && (
        <div className="space-y-6">
          {/* Connected Accounts */}
          <div className="p-6 rounded-xl bg-white/5 border border-white/10">
            <div className="flex items-center justify-between mb-4">
              <div className="flex items-center gap-2">
                <LinkIcon className="w-5 h-5 text-blue-400" />
                <h3 className="text-lg font-semibold">Connected Ad Platforms</h3>
              </div>
              {adAccounts.length > 0 && (
                <button
                  onClick={handleSyncData}
                  disabled={syncing}
                  className="flex items-center gap-2 px-4 py-2 rounded-lg bg-purple-600 hover:bg-purple-500 transition-colors disabled:opacity-50"
                >
                  <RefreshCw className={`w-4 h-4 ${syncing ? 'animate-spin' : ''}`} />
                  {syncing ? 'Syncing...' : 'Sync Data'}
                </button>
              )}
            </div>

            {adAccounts.length === 0 ? (
              <div className="text-center py-8">
                <AlertCircle className="w-12 h-12 text-gray-500 mx-auto mb-4" />
                <p className="text-gray-400 mb-2">No ad platforms connected yet</p>
                <p className="text-sm text-gray-500">
                  Connect your ad accounts using the main application to see your campaign data here.
                </p>
              </div>
            ) : (
              <div className="space-y-4">
                {adAccounts.map((account) => (
                  <div key={account.id} className="flex items-center justify-between p-4 rounded-lg bg-white/5 border border-white/10">
                    <div className="flex items-center gap-3">
                      <div className={`w-10 h-10 rounded-lg flex items-center justify-center ${
                        account.status === 'active' ? 'bg-emerald-500/20' : 'bg-yellow-500/20'
                      }`}>
                        {account.status === 'active' ? (
                          <Check className="w-5 h-5 text-emerald-400" />
                        ) : (
                          <AlertCircle className="w-5 h-5 text-yellow-400" />
                        )}
                      </div>
                      <div>
                        <p className="font-medium">{getPlatformDisplayName(account.platform)}</p>
                        <p className="text-sm text-gray-400">{account.account_name}</p>
                        {account.last_synced_at && (
                          <p className="text-xs text-gray-500">
                            Last synced: {new Date(account.last_synced_at).toLocaleString()}
                          </p>
                        )}
                      </div>
                    </div>
                    <span className={`px-3 py-1 rounded-full text-xs font-medium ${
                      account.status === 'active' 
                        ? 'bg-emerald-500/20 text-emerald-400' 
                        : 'bg-yellow-500/20 text-yellow-400'
                    }`}>
                      {account.status}
                    </span>
                  </div>
                ))}
              </div>
            )}
          </div>

          {/* Info Card */}
          <div className="p-6 rounded-xl bg-blue-500/10 border border-blue-500/30">
            <h4 className="font-semibold text-blue-300 mb-2">How to Connect Ad Platforms</h4>
            <p className="text-sm text-gray-300 mb-3">
              This experiment shares the same database as the main application. To connect your ad platforms:
            </p>
            <ol className="text-sm text-gray-400 space-y-2 list-decimal list-inside">
              <li>Go to the main application's Settings page</li>
              <li>Enter your API credentials for Google Ads, Meta Ads, or LinkedIn Ads</li>
              <li>Complete the OAuth authorization flow</li>
              <li>Return here and click "Sync Data" to refresh</li>
            </ol>
            <p className="text-xs text-gray-500 mt-4">
              Once connected, the AI chatbot can analyze your real campaign data!
            </p>
          </div>
        </div>
      )}

      {/* AI Models Tab */}
      {activeTab === 'ai-models' && (
        <div className="space-y-6">
          {/* API Keys Card */}
          <div className="p-6 rounded-xl bg-white/5 border border-white/10">
            <div className="flex items-center gap-2 mb-4">
              <Key className="w-5 h-5 text-purple-400" />
              <h3 className="text-lg font-semibold">API Keys</h3>
            </div>
            <p className="text-sm text-gray-400 mb-6">
              Configure your AI API keys for model access. Keys are stored securely.
            </p>

            <div className="space-y-4">
              {/* OpenAI Key */}
              <div>
                <label className="block text-sm font-medium mb-2">OpenAI API Key</label>
                <div className="relative">
                  <input
                    type={showKeys.openai ? 'text' : 'password'}
                    placeholder="sk-proj-..."
                    value={openaiKey}
                    onChange={(e) => setOpenaiKey(e.target.value)}
                    className="w-full px-4 py-3 rounded-lg bg-white/5 border border-white/10 focus:border-purple-500/50 focus:outline-none"
                  />
                  <button
                    type="button"
                    onClick={() => toggleKeyVisibility('openai')}
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-white"
                  >
                    {showKeys.openai ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                  </button>
                </div>
              </div>

              {/* Anthropic Key */}
              <div>
                <label className="block text-sm font-medium mb-2">Anthropic API Key</label>
                <div className="relative">
                  <input
                    type={showKeys.anthropic ? 'text' : 'password'}
                    placeholder="sk-ant-api03-..."
                    value={anthropicKey}
                    onChange={(e) => setAnthropicKey(e.target.value)}
                    className="w-full px-4 py-3 rounded-lg bg-white/5 border border-white/10 focus:border-purple-500/50 focus:outline-none"
                  />
                  <button
                    type="button"
                    onClick={() => toggleKeyVisibility('anthropic')}
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-white"
                  >
                    {showKeys.anthropic ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                  </button>
                </div>
              </div>

              {/* Google Key */}
              <div>
                <label className="block text-sm font-medium mb-2">Google AI Studio API Key</label>
                <div className="relative">
                  <input
                    type={showKeys.google ? 'text' : 'password'}
                    placeholder="AIza..."
                    value={googleKey}
                    onChange={(e) => setGoogleKey(e.target.value)}
                    className="w-full px-4 py-3 rounded-lg bg-white/5 border border-white/10 focus:border-purple-500/50 focus:outline-none"
                  />
                  <button
                    type="button"
                    onClick={() => toggleKeyVisibility('google')}
                    className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-white"
                  >
                    {showKeys.google ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                  </button>
                </div>
              </div>
            </div>
          </div>

          {/* Default Model Card */}
          <div className="p-6 rounded-xl bg-white/5 border border-white/10">
            <h3 className="text-lg font-semibold mb-4">Default AI Model</h3>
            <select
              value={defaultModel}
              onChange={(e) => setDefaultModel(e.target.value)}
              className="w-full px-4 py-3 rounded-lg bg-white/5 border border-white/10 focus:border-purple-500/50 focus:outline-none"
            >
              <optgroup label="OpenAI GPT-5">
                <option value="openai/gpt-5">GPT-5 (Flagship)</option>
                <option value="openai/gpt-5-mini">GPT-5 Mini</option>
              </optgroup>
              <optgroup label="Anthropic Claude 4.5">
                <option value="anthropic/claude-sonnet-4-5">Claude Sonnet 4.5 (Recommended)</option>
                <option value="anthropic/claude-opus-4-5">Claude Opus 4.5</option>
                <option value="anthropic/claude-haiku-4-5">Claude Haiku 4.5</option>
              </optgroup>
              <optgroup label="Google Gemini 3">
                <option value="google/gemini-3">Gemini 3</option>
                <option value="google/gemini-3-flash">Gemini 3 Flash</option>
              </optgroup>
            </select>
          </div>

          <button
            onClick={handleSaveAPIKeys}
            disabled={loading}
            className="flex items-center gap-2 px-6 py-3 rounded-lg bg-purple-600 hover:bg-purple-500 transition-colors disabled:opacity-50"
          >
            <Save className="w-5 h-5" />
            {loading ? 'Saving...' : 'Save Settings'}
          </button>
        </div>
      )}

      {/* Preferences Tab */}
      {activeTab === 'preferences' && (
        <div className="space-y-6">
          <div className="p-6 rounded-xl bg-white/5 border border-white/10">
            <h3 className="text-lg font-semibold mb-4">Display Preferences</h3>
            
            <div className="space-y-4">
              <div className="flex items-center justify-between p-4 rounded-lg bg-white/5">
                <div>
                  <p className="font-medium">Demo Mode</p>
                  <p className="text-sm text-gray-400">Show sample data when no real data is available</p>
                </div>
                <button
                  onClick={() => setDemoMode(!demoMode)}
                  className={`w-12 h-6 rounded-full transition-colors ${
                    demoMode ? 'bg-purple-600' : 'bg-gray-600'
                  }`}
                >
                  <div className={`w-5 h-5 rounded-full bg-white transition-transform ${
                    demoMode ? 'translate-x-6' : 'translate-x-0.5'
                  }`} />
                </button>
              </div>
            </div>
          </div>

          <button
            onClick={handleSaveAPIKeys}
            disabled={loading}
            className="flex items-center gap-2 px-6 py-3 rounded-lg bg-purple-600 hover:bg-purple-500 transition-colors disabled:opacity-50"
          >
            <Save className="w-5 h-5" />
            {loading ? 'Saving...' : 'Save Preferences'}
          </button>
        </div>
      )}
    </div>
  );
}
